
import Game from './engine/Game';
import Room from './engine/Room';
import { PuzzleCode } from './engine/Puzzle';
import { ActionRoom, ActionMsg } from './engine/Action';
import Image from './engine/Image';

export default function createGame(){


    const game = new Game();

    const img1 = new Image(require('./assets/book-open.png'),require('./assets/book-closed.jpg'));
    img1.zoom = true;

    const r0 = new Room("Vestíbulo", 'open');
    r0.add(`
    Estás en el vestíbulo. Ante tí hay una puerta, posas tu mano en el pomo
    pensando que intrincados acertijos deberás resolver cuando de pronto
    te das cuenta que la puerta ya está abierta.
    Un poco decepcionado prosigues con tu aventura.
    `);
    r0.add(`
    Utiliza el menú de lugares para moverte entre localizaciones.
    `);


    const rA = new Room("Salón", 'open');
    const rB = new Room("Habitación final");


    rA.add(`
        Estás en el salón, una habitación sin ninǵun tipo de mobiliario.
        En el suelo pareces distinguir un libro.
        Además hay una puerta con un candado numérico que te impide el paso
        a la habitación final.
    `);

    rA.add(img1);

    const rA_p1 = new PuzzleCode('123');
    const rA_p1_a1 = new ActionRoom(rB, 'open');
    rA_p1.add(rA_p1_a1);
    rA_p1.add(new ActionMsg(`
        Muy bien, tus habilidades investigadores superiores te han permitido solucionar la prueba.
        Ahora puedes entrar en la habitación final.
        
    `.trim()));
    rA.add(rA_p1);

    rB.add("Triunfante entras en la habitación final. Has ganado.")


    game.add(r0);
    game.add(rA);
    game.add(rB);

    return game;

}